#help {
	padding: 0;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;

	.directions {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		background: var(--color-white);
		box-shadow: 0 0 10px 2px rgba($p2020-black, 0.6);
		color: var(--color-text);
		font-size: pxrem(15);
		border-radius: 4px;
		padding: 3rem 4rem;
		display: grid;
		grid-template-columns: max-content max-content;
		grid-column-gap: 20px;
		grid-row-gap: 10px;
		pointer-events: all;
	}

	dt,
	dd {
		float: none;
		width: auto;
		padding: 0;
		margin: 0;
		text-align: left;
		font-weight: 400;
	}

	dt {
		font-family: var(--font-default);
		color: var(--color-text);
		font-weight: 400;
		border: 1px solid var(--color-border);
		box-shadow: 0 1px 0 var(--color-border);
		border-radius: 3px;
		padding: 0 8px;
		text-align: center;
	}
}
