.o2-comment-header {
	$avatar-size: 32px;
	$avatar-margin: 10px;

	@at-root header#{&} { // override upstream !important
		position: relative !important;
	}

	display: grid;
	grid-template-columns: [avatar] $avatar-size + $avatar-margin [main] auto [actions] 32px;
	align-items: center;
	margin-left: -#{$avatar-size + $avatar-margin};
	padding-bottom: 0.4em;

	.avatar {
		grid-row: 1;
		width: $avatar-size;
		height: $avatar-size;
	}

	@include breakpoint("handset-portrait") {
		margin-left: 0;
	}
}
