/**
 * Body content styles for post/pages and comments
 */

@import "css/src/ui/p2020-blockquote";
@import "css/src/ui/p2020-code";
@import "css/src/ui/p2020-h1-h6";
@import "css/src/ui/p2020-pre";
@import "css/src/ui/p2020-table";

%p2020-entry-content {
	$base-margin: #{$body-line-height}em;

	font-family: var(--font-body);
	font-size: $body-font-size;
	line-height: $body-line-height;

	// Styles for the inline-terms mu-plugin
	@extend %inline-terms;

	// Text links
	p,
	li {
		a {
			color: var(--color-link);
			text-decoration: underline;

			&:hover {
				color: var(--color-link-dark);
			}
		}
	}

	p {
		margin: 0 0 $base-margin;

		&:empty {
			margin-bottom: 0;
		}
	}

	/**
	 * Headings
	 */
	h1 { // not the entry title, but when another h1 appears in the entry content
		@extend %p2020-h1;
	}

	@extend %p2020-h2-h6;

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: #{2 * $body-font-size} 0 $body-font-size;
	}

	table {
		@extend %p2020-table;
		margin-bottom: $base-margin;
	}

	code,
	kbd {
		@extend %p2020-code;
	}

	pre {
		@extend %p2020-pre;
	}

	blockquote {
		@extend %p2020-blockquote;
	}

	blockquote,
	pre {
		margin-bottom: $base-margin;

		> :last-child {
			margin-bottom: 0;
		}
	}

	li {
		margin-bottom: 0.5em;

		> ul,
		> ol {
			margin-top: 0.5em;
		}
	}

	/**
	 * Images
	 */
	figure,
	.wp-caption,
	.alignleft,
	.alignright,
	.aligncenter,
	.alignwide,
	.alignfull {
		margin-bottom: $base-margin;
	}

	.alignleft {
		display: inline;
		float: left;
		margin-right: $base-margin;
	}

	.alignright {
		display: inline;
		float: right;
		margin-left: $base-margin;
	}

	.aligncenter {
		clear: both;
		margin-left: auto;
		margin-right: auto;
	}
}
