.p2tenberg-editor {
	.block-editor-writing-flow {
		@extend %p2020-block-editor-content;
	}

	.block-editor-inserter__toggle {
		&.is-primary {
			background: transparent;
			color: inherit;
		}
	}

	/**
	 * Temporary, p2tenberg-specific "hacks"
	 *
	 * Adds extra selector specificity to some things that will work without hacks
	 * in the core block editor, but needs some hacks here to work around the
	 * hacks in p2tenberg itself. Can be removed when the upstream hacks are gone.
	 */
	// For overriding the over-specified h2 styles in https://github.com/Automattic/p2tenberg/blob/9d8787df4fa8f84e50cb992c8c93bc2c5ba9d40a/editor/components/bridge/style.scss#L105-L114
	@at-root body.gutenberg & {
		.block-editor-writing-flow {
			@extend %p2020-h2-h6;
		}
	}
}

.p2tenberg-editor__newpost {
	border: 0;
	background: transparent;

	.edit-post-header {
		border-radius: 4px 4px 0 0;
	}

	.block-editor-block-list__layout:first-child {
		@extend %p2020-post-responsive-padding;

		// Workaround for top toolbar overlap
		// https://github.com/Automattic/p2tenberg/issues/196
		padding-top: 2.5em;

		&:not(.is-expanded) {
			padding-top: 0;
			padding-bottom: 0;
		}

		// Full-width images
		@extend %p2020-alignfull;

		// Hack to prevent first paragraph from jumping up on focus.
		// Can be removed if it doesn't happen anymore due to upstream changes.
		p:first-child {
			margin-top: #{$body-line-height}em;
		}
	}
}
