.p2020-searchform {
	display: flex;
	align-items: center;
	font-size: pxrem(14);
	border-radius: 4px;
	margin-left: 28px;
	padding: 0 8px 0 4px;
	box-shadow: $shadow-input, 0 0 0 1px var(--color-border) inset;

	&::before {
		@include icon("custom-search", pxrem(24), var(--color-text-light));
		margin-right: 4px;
		margin-bottom: 1px;
	}

	&:focus-within {
		box-shadow: 0 0 0 1px var(--color-border-dark) inset;
	}
}

.p2020-searchform__field {
	border: 0;
	font-family: var(--font-default);
	background: transparent;
	padding: 9px 0;
	width: 15em;

	&::placeholder {
		color: var(--color-text-light);
		opacity: 1;
	}

	&:focus {
		&::placeholder {
			opacity: 0.3;
		}
	}
}

