:root {

	/* COLORS */
	--color-text: #2c3338;
	--color-text-light: #858d9a;

	--color-link: #{$p2020-blue};
	--color-link-dark: #{darken($p2020-blue, 13%)};

	--color-border: #dbdde1;
	--color-border-light: #e9eaed;
	--color-border-dark: #75808a;

	--color-background-very-light: #f7f8fa;
	--color-background-light: #e8e9ed;
	--color-background-dark: #2c3338;
	--color-background-link: #dfeafb;

	--color-mentions: #{$p2020-purple};
	--color-mentions-highlight: #{rgba($p2020-purple, 0.1)};

	--color-white: #{$p2020-white};
	--color-black: #{$p2020-black};
	--color-green: #1CCA7B;
	--color-green-light: #d4f8d4;

	/* TYPOGRAPHY */
	--font-default: #{$font-sans};
	--font-body: #{$font-sans};
	--font-mono: #{$font-mono};

	/* ANIMATION */
	--transition-quick: 0.1s;
}
