.p2020-menu-primary {
	$top-level-side-padding: 12px;

	font-size: pxrem(14);

	a {
		display: block;
		padding: 8px 18px;
		color: var(--color-text);
		text-overflow: hidden;
		white-space: nowrap;
		overflow: hidden;
	}

	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		line-height: 1.4;
	}

	.menu-item-has-children {
		> a {
			display: flex;
			justify-content: space-between;
			align-items: center;

			&::after {
				@include icon("custom-chevron", 18px, currentColor);

				margin-left: 2px;
			}
		}
	}

	// Top level items
	> .menu > .menu-item > a {
		padding: 15px $top-level-side-padding;
	}

	.menu {
		display: flex;

		@include breakpoint("tablet-portrait") {
			flex-direction: column;
		}

		> .current-menu-item,
		> .current-menu-parent {
			&::after {
				content: "";
				display: block;
				position: absolute;
				bottom: -1px;
				left: $top-level-side-padding;
				right: $top-level-side-padding;
				height: 2px;
				background: var(--color-link);
				z-index: 0;
			}
		}
	}

	.sub-menu {
		position: absolute;
		top: calc(100% - 5px);
		left: 9px;
		display: none;
		padding: 8px 0;
		border-radius: 4px;
		box-shadow: $shadow-popover;
		background: var(--color-white);
		z-index: 1;

		.sub-menu {
			top: -2px;
			left: calc(100% - 4px);

			@include breakpoint( "tablet-portrait" ) {
				position: relative;

				.sub-menu {
					top: 0;
					left: 0;
				}
			}
		}

		.menu-item-has-children {
			> a::after {
				transform: rotate(-90deg);
				margin-left: 16px;
			}
		}
	}

	.menu-item {
		position: relative;
		margin: 0;
		padding: 0;
		white-space: nowrap;

		.menu-item {
			white-space: normal;
			min-width: 14em;
			max-width: 20em;

		}

		&:hover,
		&:focus-within {
			background: var(--color-background-very-light);

			> .sub-menu {
				display: block;
			}
		}
	}

	@include breakpoint( "tablet-portrait" ) {
		display: none;
	}
}
