.o2-no-posts-post {
	font-size: pxrem(24);
	font-weight: 600;
	letter-spacing: -0.02em;
	height: 70vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	.o2-post {
		padding: 0;
	}

	p {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 0;

		&:before {
			content: "";
			margin-bottom: 16px;
			@include icon("big-checkmark", pxrem(64), var(--color-green));
		}
	}
}