.wp-block-code {
	@extend %p2020-pre;

	padding: 2em;
	border: 0;
	border-radius: 0;
	font-family: var(--font-mono);
	line-height: 1.4;
	color: var(--color-text);

	code {
		padding: 0;
		font-size: inherit;
	}

	.block-editor-plain-text {
		border: 0;
		background: transparent;
		font-family: var(--font-mono);
	}
}
