.p2020-unread-post,
.p2020-unread-comment,
.p2020-unread-mention {
	&::before {
		content: "NEW";
		color: var(--color-green);
		background: var(--color-green-light);
		position: absolute;
		top: 0;
		border-radius: 0 0 3px 3px;
		text-transform: uppercase;
		font-size: 0.625rem;
		font-weight: 600;
		letter-spacing: 1px;
		padding: 0.25rem 0.75rem;
	}

	&.post {
		&::before {
			left: 24px;
		}
	}

	&.comment {
		&::before {
			transform: translateX(-42px);
		}
	}
}

