.o2-display-comments-toggle {

	margin-top: 20px;
	margin-left: 1px;

	a {
		display: inline-flex;
		align-items: center;
		text-transform: lowercase;

		.disclosure-text::first-letter {
			text-transform: uppercase;
		}

		&:not(:hover) {
			color: var(--color-text);
		}
	}

	.disclosure-text {
		margin-left: 4px;
	}
}

