%p2020-article {
	overflow-wrap: break-word;
	margin: 0 0 30px;
	border-radius: 4px;
	background: var(--color-white) !important; // override o2 making newly posted articles transparent
	box-shadow: $shadow-card;
	font-size: $body-font-size;
	position: relative;

	&.sticky {
		border: 3px solid var(--color-link);

		&::before {
			content: "Pinned";
			color: var(--color-link);
			background: var(--color-background-link);
			position: absolute;
			top: 0;
			left: 24px;
			border-radius: 0 0 3px 3px;
			text-transform: uppercase;
			font-size: pxrem(10);
			font-weight: 600;
			letter-spacing: 1px;
			padding: pxrem(4) pxrem(12);


			@include breakpoint("handset-portrait") {
				display: none;
			}
		}
	}

	// Full-width images
	@extend %p2020-alignfull;
}
