.navigation {
	margin: 0 0 2em;
	padding: 0;
	overflow: hidden;
	border-top: 1px solid var(--color-border);
	display: flex;

	p {
		width: 50%;

		&.nav-newer {
			float: right;
			text-align: right;
		}
	}

	a {
		display: flex;
		height: 100%;
		padding: 1.2em;
		color: var(--color-text-light);
	}

	.previous,
	.nav-older {
		margin: 0;
		padding: 0;
		width: 50%;
	}

	.next,
	.nav-newer {
		margin: 0;
		padding: 0;
		width: 50%;

		a {
			justify-content: flex-end;
		}
	}

	.nav-older a {
		border-right: 1px solid var(--color-border);
	}

	a:hover {
		color: var(--color-link);
	}

}
