.p2020-site-header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 0 16px;
	border-bottom: 1px solid var(--color-border);
	background: var(--color-white);
	min-height: 50px;

	@include breakpoint("tablet-portrait") {
		padding: 12px;
	}
}

.p2020-site-header__main {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
