/**
 * Colors
 */
$p2020-blue: #0267ff;
$p2020-purple: #b35eb1;

$p2020-white: #fff;
$p2020-black: #000;

/**
 * Typography
 */
$font-sans: inter, -apple-system, system-ui, blinkmacsystemfont, "Segoe UI", "Roboto", "Oxygen-Sans", "Ubuntu", "Cantarell", "Helvetica Neue", sans-serif;
$font-mono: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", "Courier New", monospace;

$body-font-size: pxrem(16);
$body-line-height: 1.7;

/**
 * Shadows
 */
$shadow-popover: 0 0 10px 2px rgba($p2020-black, 0.07);
$shadow-card: 0 1px 2px rgba($p2020-black, 0.1);
$shadow-input: 0 1px 2px rgba($p2020-black, 0.1) inset;

/**
 * WP Admin Bar
 */
$wpadminbar-height: 32px;
$wpadminbar-height-mobile: 46px;
$wpadminbar-breakpoint: 782px;

/**
 * Breakpoints
 */
$web: 1200px;
$tablet-landscape: 1024px;
$tablet-portrait: 876px;
$handset-landscape: 640px;
$handset-portrait: 550px;

$breakpoints: (
	"web": $web,
	"tablet-landscape": $tablet-landscape,
	"tablet-portrait": $tablet-portrait,
	"handset-landscape": $handset-landscape,
	"handset-portrait": $handset-portrait,
	"wpadminbar": $wpadminbar-breakpoint,
);

/**
 * Spacing
 */
// Article padding
$horizontal-padding-default: 10%;
$horizontal-padding-narrow: 2em;
$responsive-padding-breakpoint: "handset-portrait";
