#o2-dock {
	width: 340px;
	background: var(--color-white);
	box-shadow: $shadow-popover;

	@media (max-width: 876px) {
		width: 100%;
	}


	.o2-dock-count {
		background-color: var(--color-link);
		font-family: inherit;
		font-size: pxrem(13);
		font-weight: 600;
		padding: 0 10px;
		line-height: 30px;
		height: 30px;
		border: none;
		position: absolute;
		display: block;
		top: -30px;
		right: 0;

		&::before {
			display: none;
		}

		&::after {
			content: " new update(s)";
		}

		@media (max-width: 876px) {
			top: -38px;
			height: 38px;
			line-height: 38px;
		}
	}

	#o2-items-scroll {
		max-height: 350px;
		overflow: auto;
	}

	.o2-dock-items {
		border: none;
		background: none;
		color: inherit;

		&:first-child {
			margin-top: 8px;
		}

		&:last-child {
			margin-bottom: 8px;
		}

		li {
			&.o2-notification {
				display: flex !important;
				font-family: inherit;
				font-size: pxrem(13);
				padding: 8px 14px;

				a {
					color: var(--color-link);

					&:hover {
						color: var(--color-link-dark);
					}
				}

				.entry-date {
					color: var(--color-text-light);
					font-size: pxrem(12);
					display: block;
				}
			}

			.avatar {
				float: none;
				width: 32px;
				height: 32px;
			}

			p {
				flex: 2;
				float: none;
				width: auto;
				margin: 0 12px;
				line-height: 1.4;
			}

			span.o2-notification-close {
				order: 3;
				float: none;

				a.o2-notification-close {
					color: var(--color-text-light);

					&:hover {
						color: var(--color-link);
					}
				}
			}
		}
	}

	#o2-dock-controls {
		border: 1px solid var(--color-border-light);
		border-width: 1px 0 0;
		background: var(--color-white);
		font-family: inherit;
		color: var(--color-text-light);
		margin: 0 16px;
		padding: 8px 0;
		text-transform: lowercase;

		&::first-letter {
			text-transform: uppercase;
		}
	}
}
