@mixin icon($name, $size, $color) {
	content: "";
	display: inline-block;
	mask: url(img/icons/#{$name}.svg) no-repeat;
	mask-size: cover;
	width: $size;
	height: $size;
	background: $color;
}


@mixin breakpoint($size) {
	@each $breakpoint, $value in $breakpoints {
		@if $size == $breakpoint {
			@media (max-width: map-get($breakpoints, $breakpoint)) {
				@content;
			}
		}
	}
}

// Break out of the parent container's horizontal padding
@mixin breakout-container-padding($padding) {
	@if comparable($padding, 0%) { // if $padding is percentage
		$breakout-multiplier: 100% / (100% - 2 * $padding);

		margin-left: -#{$padding * $breakout-multiplier};
		margin-right: -#{$padding * $breakout-multiplier};
		width: percentage($breakout-multiplier);
	} @else {
		margin-left: -#{$padding};
		margin-right: -#{$padding};
		width: calc(100% + #{2 * $padding});
	}
}

// Break out of the parent container's responsive horizontal padding
@mixin breakout-container-padding-responsive($padding-default, $padding-narrow, $breakpoint: "handset-portrait") {
	@include breakout-container-padding($padding-default);

	@include breakpoint($breakpoint) {
		@include breakout-container-padding($padding-narrow);
	}
}
