.p2020-mobile-new-post {
	display: none;
	min-height: 32px;
	margin-bottom: 1rem;

	@include breakpoint("handset-landscape") {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		visibility: hidden; // will be made visible by JS
	}
}

.p2020-mobile-new-post__new {
	padding: 0;
	border: 0;
	appearance: none;
	line-height: 1;

	&::before {
		@include icon("custom-add", 24px, var(--color-text));
	}
}

.p2020-mobile-new-post__cancel {
	display: none;
	padding: 0;
	border: 0;
	appearance: none;
	font-size: pxrem(14);
	font-weight: 500;
}
