// TODO: This file hasn't been tested or cleaned yet because we don't support
// cross-posting yet.

.tag-p2-xpost {
	background: transparent !important;
	box-shadow: none;
	margin-bottom: 0;
	border-top: 1px solid var(--color-border-light);
	border-radius: 0;
	padding: 40px $horizontal-padding-default;
	position: relative;

	:not(.tag-p2-xpost) + & {
		border-top: none;
		margin-top: -30px;
	}

	.avatar {
		width: 32px;
		float: left;
		margin-top: 2px;
		box-shadow: none;
		border: none;
	}

	.o2-post,
	.o2-comment {
		font-size: pxrem(14);
		padding: 0;
		font-style: normal;

		.avatar {
			width: 32px;
		}
	}

	.o2-post {
		background: transparent;
		min-height: 36px;
		position: relative;

		&::before {
			content: "";
			display: flex;
			position: absolute;
			width: 24px;
			height: 24px;
			left: -40px;
			top: 6px;
			opacity: 0.5;
			transition: opacity 0.2s;

			@include icon("custom-xpost", pxrem(24), var(--color-text-light));
		}
	}

	&:hover {
		.o2-post::before {
			opacity: 1;
		}
	}

	.author-avatar {
		display: block;

		img {
			margin-right: 10px;
		}
	}

	.o2-comment {
		padding-left: 5em;

		.o2-comment-header {
			overflow: visible;
			padding-bottom: 0;
		}

		.comment-meta {
			line-height: 1.4;
		}

		.o2-actions {
			display: none;
		}
	}

	.o2-tags-array,
	.sharedaddy {
		display: none;
	}

	p {
		margin: 0;
	}

	.comment-likes {
		display: none;
	}

}

article.tag-p2-xpost {
	.comment {
		.avatar {
			margin-left: -10px;
			margin-top: 0;
		}
	}
}

.o2-xpost-author {
	overflow: hidden;
	line-height: 1.3;

	a {
		padding: 0;
	}

	.entry-author {
		font-size: pxrem(13);
		font-weight: 600;
	}

	.entry-date {
		color: var(--color-text-light);
		font-size: pxrem(12);
		font-weight: 400;
		padding-left: 5px;
	}
}

.o2-xpost-avatar {
	border-width: 0;
	box-shadow: none;
}

.o2-xpost-entry-date {
	float: none;

	&:hover {
		color: var(--color-link);
	}
}

@include breakpoint( "handset-portrait" ) {
	.tag-p2-xpost {
		margin: 0 0 0.5em !important;
	}

}

.o2-post .o2-xpost-content {
	padding-left: 44px;
	line-height: 1.4;
	font-size: pxrem(13);
	color: var(--color-text-light);
}

.o2-post .o2-xpost-content p {
	margin-top: 0;
	margin-bottom: 0;
}
