.p2020-sidebar__header-image {
	display: block;
}

.p2020-sidebar__inner-main {
	padding: 40px;

	ul {
		list-style-type: none;
		margin: 0;

		ul {
			margin-left: 1em;
		}
	}

	@include breakpoint( "tablet-portrait" ) {
		padding: 20px;
	}
}

.p2020-sidebar__footer {
	margin-top: 4em;
	color: var(--color-text-light);
	font-size: pxrem(13);

	&::before {
		content: "";
		display: block;
		border-top: 1px solid var(--color-border);
		width: 32px;
		margin-bottom: 1.25em;
	}

	button { // reset
		margin: 0;
		padding: 0;
		border: 0;
		background: transparent;
		appearance: none;
		box-shadow: none;
		font-weight: inherit;
	}

	a,
	button {
		color: currentColor;

		&:hover {
			color: var(--color-link);
		}
	}
}

.p2020-sidebar__footer-links {
	font-weight: 500;

	li + li {
		margin-top: 0.5em;
	}
}
