.o2-comment {
	padding: 2em $horizontal-padding-default;
	border-top: 1px solid var(--color-border);
	position: relative;

	.o2-comment-footer-actions {
		padding-bottom: 0;

		li,
		p {
			margin-bottom: 0;
		}
	}

	@include breakpoint( "handset-portrait" ) {
		padding: $horizontal-padding-narrow;
	}

	&#respond {
		.o2-timestamp {
			display: none;
		}
	}

	.o2-editor {
		margin: 8px 0;
	}
}

// Nested comments
.o2-child-comments {
	padding-left: 2.5em;

	.o2-comment {
		padding-left: 0;
		padding-right: 0;
		padding-bottom: 0;
		border-top: none;
	}

	@include breakpoint( "handset-portrait" ) {
		padding-top: 2em;
		padding-left: 0;

		.o2-comment {
			padding: 4px 0 4px 1em;
		}
	}
}

.o2-comment-awaiting-approval {
	font-style: italic;
	color: var(--color-text-light);
}
