body {
	display: flex;
	flex-direction: column;
	min-height: calc(100vh - #{$wpadminbar-height});

	@include breakpoint("wpadminbar") {
		min-height: auto;
	}
}

#page {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
}

#masthead {
	position: sticky;
	top: 0; // for when there's no admin bar, like in the Customizer
	z-index: 1;
	width: 100%;

	@at-root .admin-bar & {
		top: $wpadminbar-height;

		@include breakpoint("wpadminbar") {
			top: $wpadminbar-height-mobile;
		}
	}
}

#main-wrapper {
	display: flex;
	flex-grow: 1;
}

#content {
	position: relative;
	z-index: 0;
	margin: 0 auto;
	width: 75%;
	max-width: 1020px; // should be in sync with $content_width in functions.php
	padding: 50px 40px;

	@include breakpoint( "tablet-portrait" ) {
		width: 100%;
		padding: 16px;
	}
}

#sidebar {
	width: 25%;
	max-width: 360px;
	min-width: 280px;
	border-right: 1px solid var(--color-border);

	@include breakpoint( "tablet-portrait" ) {
		position: absolute;
		top: 0;
		left: -200%;
		right: 0;
		z-index: 1;
		transition: left 0.25s ease-in-out;
		width: 100%;
		margin: 12px auto;
		border: 0;
		border-radius: 4px;
		background: var(--color-white);
		box-shadow: $shadow-popover;

		@at-root .mobile-menu-is-visible & {
			left: 0;
		}
	}
}
