.o2-app-controls {
	visibility: hidden;
	float: none;
	display: flex;
	justify-content: flex-end;
	font-size: pxrem(12);
	margin-top: 0;
	margin-left: auto;

	.o2-toggle-comments {
		visibility: visible;
		order: 3;
		color: var(--color-text-light);
	}

	.o2-toggle-keyboard-help {
		display: none;
	}

	@include breakpoint("tablet-portrait") {
		display: none;
	}
}
