.more-link {
	display: block;
	padding: 0.5em;
	border: 1px solid var(--color-border);
	border-radius: 4px;
	text-align: center;
	font-size: pxrem(14);
	margin-top: 24px;
	transition: var(--transition-quick);

	&:hover {
		border-color: var(--color-link);
		box-shadow: 0 1px 2px rgba($p2020-black, 0.08);
	}
}

.o2-extended-more {
	margin-top: 24px;
}
