/**
 * Styles for a blockquote
 */
%p2020-blockquote {
	background: var(--color-background-very-light);
	font-style: italic;
	margin: 0 0 1.2em;
	padding: 2em;
	overflow: auto;

	code {
		border: 1px solid var(--color-border);
		background: var(--color-white);
	}

	em {
		font-style: normal;
	}

	cite {
		@extend %p2020-blockquote__cite;
	}

	&.wp-embedded-content {
		padding: 1.2em;
	}
}

%p2020-blockquote__cite {
	display: block;
	margin-top: 1em;
	text-align: right;

	&::before {
		content: "—\0000a0"; // em dash and non-breaking space
	}
}
