.widget {
	font-size: pxrem(14);
	line-height: 1.4;

	& + & {
		margin-top: 3em;
	}

	select {
		max-width: 100%;
	}
}

.widget-title {
	margin-bottom: pxrem(16);
	font-size: pxrem(18);
	font-weight: 500;
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
}

.widget-title-secondary-action {
	font-size: pxrem(12);
	font-weight: 400;
	color: var(--color-text-light);

	&:hover,
	&:focus {
		color: var(--color-link);
	}
}

/**
 * Core widgets
 */
.widget_media_image,
.widget_custom_html {
	img {
		display: block;
		border-radius: 4px;
	}
}

.widget_calendar {
	.wp-calendar-table {
		margin-bottom: 8px;

		caption { /* Month & Year */
			font-size: 1.1em;
			padding: 0.4em;
			text-align: center;
		}

		th,
		td {
			text-align: center;
			padding: 0.3em;
		}
	}
}

.widget_recent_comments {
	.recentcommentstexttop,
	.recentcommentstextend {
		vertical-align: middle;
	}
}

/**
 * P2020 widgets
 */

.widget_p2020-my-team-widget {
	.widgets-multi-column-grid ul {
		display: flex;
		flex-wrap: wrap;
		margin: -8px;

		li {
			margin: 7px 7px;
			padding: 0;
			float: none;
			flex-shrink: 0;
		}

		.widget-myteam-item {
			display: flex;
			border-radius: 35px;
			width: 35px;
			height: 35px;
			padding: 0;
			align-items: center;
			justify-content: center;
			color: var(--color-text);
			font-size: pxrem(12);
			font-weight: 500;
			transition: var(--transition-quick);
		}

		a.widget-myteam-item {
			&:hover,
			&:focus {
				outline: 0;
				box-shadow: 0 0 0 2px var(--color-link);
			}
		}

		.widget-myteam-manage-icon {
			.widget-myteam-item {
				border: 1px solid var(--color-border);
			}

			a.widget-myteam-item {
				&:hover,
				&:focus {
					border-color: transparent;
					color: var(--color-link);
				}
			}

			span.widget-myteam-item {
				cursor: default;
			}

			.widget-myteam-manage-icon-plus {
				@include icon("custom-add", pxrem(24), currentColor);
			}
		}
	}

}

@mixin widget-p2020-filter-gridicon($id-slug, $icon-name) {
	#widget-filter-#{$id-slug} .widget-filter-item-link-icon::before {
		@include icon("custom-#{$icon-name}", pxrem(24), currentColor);
	}
}

.widget_p2020-filter-widget {
	#widget-filter-me {
		margin-top: pxrem(40);
	}

	.widget-filter-me-title {
		margin-bottom: pxrem(12);
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-end;
	}

	.widget-filter-me-label {
		display: inline-block;
		font-size: pxrem(18);
		font-weight: 500;
	}

	.widget-filter-item {
		color: var(--color-text);
		line-height: 1.3;
		margin: 0 pxrem(-10);
		padding: pxrem(1) 0;

		.widget-filter-item-link {
			color: inherit;
			display: flex;
			align-items: center;
			padding: pxrem(6) pxrem(10);
			border-radius: 4px;
			transition: var(--transition-quick);

			&:hover,
			&:focus {
				background: var(--color-background-light);
				outline: 0;
			}
		}

		&.is-active {
			.widget-filter-item-link {
				color: var(--color-link);
				background: var(--color-background-link);
			}

		}

		.widget-filter-item-link-icon {
			margin-right: 0.5em;
			display: flex;
		}

		.widget-filter-item-link-label {
			flex: 2;
			vertical-align: top;
			font-size: pxrem(13);
			font-weight: 500;
		}

		.widget-filter-item-link-unread {
			justify-self: flex-end;
			background: var(--color-link);
			color: var(--color-white);
			border-radius: 100px;
			font-size: pxrem(11);
			font-weight: 500;
			padding: pxrem(3) pxrem(8);
		}
	}

	// Icons
	@include widget-p2020-filter-gridicon("recent-updates", "posts");
	@include widget-p2020-filter-gridicon("recent-comments", "comment");
	@include widget-p2020-filter-gridicon("my-mentions", "mention");
	@include widget-p2020-filter-gridicon("my-posts", "user");
}
