%p2020-h1 {
	font-family: var(--font-default);
	font-weight: 900;
	font-size: pxrem(32);
	line-height: 1.35;
	letter-spacing: -0.02em;

	@include breakpoint("tablet-portrait") {
		font-size: pxrem(26);
	}
}

%p2020-h2-h6 {
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-family: var(--font-default);
		line-height: 1.1;
		font-weight: 700;
	}

	h2 {
		font-size: pxrem(24);
	}

	h3 {
		font-size: pxrem(20);
	}

	h4 {
		font-size: pxrem(16);
		text-transform: uppercase;
	}

	h5 {
		font-size: pxrem(13);
		text-transform: uppercase;
	}

	h6 {
		font-size: pxrem(11);
		text-transform: uppercase;
	}

	@include breakpoint("tablet-portrait") {
		h2 {
			font-size: pxrem(21);
		}

		h3 {
			font-size: pxrem(18);
		}
	}
}
